<template>
  <div class="container">
    <div class="row justify-content-center align-items-center min-vh-100">
      <div class="col-md-6 col-lg-4">
        <div class="card shadow-sm">
          <div class="card-body p-4">
            <h2 class="card-title text-center mb-4">POS Login</h2>
            <form @submit.prevent="handleLogin">
              <div class="mb-3">
                <label for="pin" class="form-label">PIN</label>
                <input
                  type="password"
                  class="form-control"
                  id="pin"
                  v-model="pin"
                  required
                  autocomplete="current-password"
                  :disabled="isLoading"
                >
              </div>
              <p v-if="errorMessage" class="alert alert-danger py-2">{{ errorMessage }}</p>
              <button type="submit" class="btn btn-primary w-100 mt-3" :disabled="isLoading">
                <span v-if="isLoading" class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                <span v-if="isLoading" class="ms-1">Logging in...</span>
                <span v-else>Login</span>
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useRouter } from 'vue-router';

export default {
  name: 'UserLogin',
  data() {
    return {
      pin: '',
      errorMessage: '',
      isLoading: false
    };
  },
  setup() {
    const router = useRouter();
    return { router };
  },
  methods: {
    handleLogin() {
      this.isLoading = true;
      this.errorMessage = '';

      setTimeout(() => {
        const MOCK_PIN = '1234'; 

        if (this.pin === MOCK_PIN) {
          sessionStorage.setItem('isLoggedInPos', 'true');
          this.router.push({ name: 'POS' });
        } else {
          this.errorMessage = 'Invalid PIN.';
        }
        this.isLoading = false;
      }, 1500);
    }
  }
};
</script>

<style scoped>
.min-vh-100 {
  min-height: 100vh;
}
.btn .spinner-border-sm {
  vertical-align: text-bottom;
}
</style>
