<template>
  <div class="card shadow-sm">
    <div class="card-header bg-light">
      <h2 class="h5 mb-0">Shopping Cart</h2>
    </div>
    <div class="card-body">
      <div v-if="cartItems.length === 0" class="alert alert-secondary">
        Your cart is empty.
      </div>
      <ul v-else class="list-group list-group-flush mb-3">
        <li v-for="item in cartItems" :key="item.productId" class="list-group-item px-0">
          <div class="d-flex justify-content-between align-items-center mb-1">
            <div>
              <span class="fw-bold">{{ item.name }}</span><br>
              <small class="text-muted">${{ item.price.toFixed(2) }} x {{ item.quantity }}</small>
            </div>
            <span class="fw-bold text-success">${{ (item.price * item.quantity).toFixed(2) }}</span>
          </div>
          <div class="d-flex justify-content-end align-items-center">
            <div class="btn-group btn-group-sm me-2" role="group" aria-label="Quantity controls">
              <button @click="decrementQuantity(item.productId)" class="btn btn-outline-secondary" :disabled="isCheckingOut">-</button>
              <button class="btn btn-outline-secondary disabled" style="min-width: 30px;">{{ item.quantity }}</button>
              <button @click="incrementQuantity(item.productId)" class="btn btn-outline-secondary" :disabled="isCheckingOut">+</button>
            </div>
            <button @click="removeItem(item.productId)" class="btn btn-danger btn-sm" :disabled="isCheckingOut">
              <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash3-fill" viewBox="0 0 16 16">
                <path d="M11 1.5v1h3.5a.5.5 0 0 1 0 1h-.538l-.853 10.66A2 2 0 0 1 11.115 16h-6.23a2 2 0 0 1-1.994-1.84L2.038 3.5H1.5a.5.5 0 0 1 0-1H5v-1A1.5 1.5 0 0 1 6.5 0h3A1.5 1.5 0 0 1 11 1.5m-5 0v1h4v-1a.5.5 0 0 0-.5-.5h-3a.5.5 0 0 0-.5.5M4.5 5.029l.5 8.5a.5.5 0 1 0 .998-.06l-.5-8.5a.5.5 0 1 0-.998.06m6.53-.528a.5.5 0 0 0-.528.47l-.5 8.5a.5.5 0 0 0 .998.058l.5-8.5a.5.5 0 0 0-.47-.528M8 4.5a.5.5 0 0 0-.5.5v8.5a.5.5 0 0 0 1 0V5a.5.5 0 0 0-.5.5"/>
              </svg>
            </button>
          </div>
        </li>
      </ul>


      <div v-if="cartItems.length > 0" class="border-top pt-3">
        <div class="d-flex justify-content-between align-items-center">
          <h3 class="h5 mb-0">Total:</h3>
          <h3 class="h5 mb-0 fw-bold text-primary">${{ totalPrice.toFixed(2) }}</h3>
        </div>
        <button @click="performCheckout" class="btn btn-warning w-100 mt-3 fw-bold" :disabled="isCheckingOut || cartItems.length === 0">
          <span v-if="isCheckingOut" class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
          <span v-if="isCheckingOut" class="ms-1">Processing...</span>
          <span v-else>Checkout</span>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ShoppingCart',
  props: {
    cartItems: { /* ... */
      type: Array,
      required: true,
      default: () => []
    },
    isCheckingOut: { // New prop to receive loading state
      type: Boolean,
      default: false
    }
  },
  // ... computed ...
  computed: {
    totalPrice() {
      return this.cartItems.reduce((total, item) => total + (item.price * item.quantity), 0);
    }
  },
  methods: {
    // ... incrementQuantity, decrementQuantity, removeItem ...
    incrementQuantity(productId) {
      this.$emit('update-quantity', { productId, change: 1 });
    },
    decrementQuantity(productId) {
      const item = this.cartItems.find(item => item.productId === productId);
      if (item && item.quantity > 0) {
        this.$emit('update-quantity', { productId, change: -1 });
      }
    },
    removeItem(productId) {
      this.$emit('remove-item', productId);
    },
    performCheckout() { // Renamed to avoid conflict with event name
      if (this.cartItems.length > 0) {
        this.$emit('checkout', this.totalPrice); // Emit event for PosSystem to handle
      } else {
        // This case should ideally be prevented by disabling the button
        alert("Your cart is empty!");
      }
    }
  }
};
</script>

<style scoped>
/* ... styles ... */
.list-group-item.px-0 {
    padding-left: 0 !important;
    padding-right: 0 !important;
}
.btn .spinner-border-sm {
  vertical-align: text-bottom;
}
</style>