<template>
  <div class="card shadow-sm">
    <div class="card-header bg-light">
      <h2 class="h5 mb-0">Products</h2>
    </div>
    <div class="card-body p-0"> <div v-if="products.length === 0" class="alert alert-info m-3">
        No products available.
      </div>
      <ul v-else class="list-group list-group-flush">
        <li v-for="product in products" :key="product.id" class="list-group-item d-flex justify-content-between align-items-center">
          <div>
            <span class="fw-bold">{{ product.name }}</span>
            <br>
            <small class="text-muted">${{ product.price.toFixed(2) }}</small>
          </div>
          <button @click="addToCart(product)" class="btn btn-success btn-sm">
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-cart-plus-fill me-1" viewBox="0 0 16 16">
              <path d="M.5 1a.5.5 0 0 0 0 1h1.11l.401 1.607 1.498 7.985A.5.5 0 0 0 4 12h1a2 2 0 1 0 0 4 2 2 0 0 0 0-4h7a2 2 0 1 0 0 4 2 2 0 0 0 0-4h1a.5.5 0 0 0 .491-.408l1.5-8A.5.5 0 0 0 14.5 3H2.89l-.405-1.621A.5.5 0 0 0 2 1zM6 14a1 1 0 1 1-2 0 1 1 0 0 1 2 0m7 0a1 1 0 1 1-2 0 1 1 0 0 1 2 0M9 5.5V7h1.5a.5.5 0 0 1 0 1H9v1.5a.5.5 0 0 1-1 0V8H6.5a.5.5 0 0 1 0-1H8V5.5a.5.5 0 0 1 1 0"/>
            </svg>
            Add
          </button>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ProductList',
  props: {
    products: {
      type: Array,
      required: true,
      default: () => []
    }
  },
  methods: {
    addToCart(product) {
      this.$emit('add-to-cart', product);
    }
  }
};
</script>

<style scoped>
/* Most custom styles for ProductList are replaced by Bootstrap.
   You can keep this for fine-tuning if needed.
   fw-bold, text-muted, btn-sm, etc., are Bootstrap classes.
*/
</style>