import { createRouter, createWebHistory } from "vue-router";
import Login from "../components/Login.vue";
import PosSystem from "../components/PosSystem.vue"; // We'll create this wrapper component
import NotFound from "../components/NotFound.vue"; // A component for 404 pages

// Helper function to check login status (can be more sophisticated)
const isLoggedIn = () => {
  return sessionStorage.getItem("isLoggedInPos") === "true";
};

const routes = [
  {
    path: "/login",
    name: "Login",
    component: Login,
    // If already logged in, redirect to POS
    beforeEnter: (to, from, next) => {
      if (isLoggedIn()) {
        next({ name: "POS" });
      } else {
        next();
      }
    },
  },
  {
    path: "/", // Default path, could also be '/pos'
    name: "POS",
    component: PosSystem,
    meta: { requiresAuth: true }, // Add meta field to indicate protected route
  },
  // It's good practice to have a catch-all for 404 pages
  {
    path: "/:pathMatch(.*)*", // This regex matches any path not defined above
    name: "NotFound",
    component: NotFound,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL), // or createWebHashHistory() for hash mode
  routes,
});

// Navigation Guard: Global "beforeEach"
router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth && !isLoggedIn()) {
    next({ name: "Login" });
  } else {
    // Otherwise, proceed
    next();
  }
});

export default router;
