<template>
  <div class="container-fluid"> <header class="bg-primary text-white p-3 mb-4 rounded d-flex justify-content-between align-items-center shadow-sm">
      <h1 class="h4 mb-0">My Mini POS System</h1>
      <button @click="handleLogout" class="btn btn-warning btn-sm">Logout</button>
    </header>

    <main>
      <div class="row g-4"> <div class="col-lg-7 col-md-6"> <ProductList :products="products" @add-to-cart="handleAddToCart" />
        </div>
        <div class="col-lg-5 col-md-6">
          <ShoppingCart
            :cart-items="cart"
            @update-quantity="handleUpdateQuantity"
            @remove-item="handleRemoveItem"
            @checkout="handleCheckout"
          />
        </div>
      </div>
    </main>

   
  </div>
</template>

<script>
import ProductList from './ProductList.vue';
import ShoppingCart from './ShoppingCart.vue';
import { useRouter } from 'vue-router';

export default {
  name: 'PosSystem',
  components: {
    ProductList,
    ShoppingCart
  },
  data() {
    return {
      products: [
        { id: 'P1001', name: 'Organic Apples (1kg)', price: 4.99 },
        { id: 'P1002', name: 'Whole Wheat Bread', price: 3.50 },
        { id: 'P1003', name: 'Free-Range Eggs (12ct)', price: 5.25 },
        { id: 'P1004', name: 'Almond Milk (1L)', price: 2.99 },
        { id: 'P1005', name: 'Dark Chocolate Bar (70%)', price: 3.15 },
        { id: 'P1006', name: 'Avocado (Single)', price: 1.75 }
      ],
      cart: []
    };
  },
  computed: {
    currentYear() {
      return new Date().getFullYear();
    }
  },
  setup() {
    const router = useRouter();
    return { router };
  },
  methods: {
    handleLogout() {
      sessionStorage.removeItem('isLoggedInPos');
      this.router.push({ name: 'Login' });
    },
    handleAddToCart(productToAdd) {
      const existingItem = this.cart.find(item => item.productId === productToAdd.id);
      if (existingItem) {
        existingItem.quantity++;
      } else {
        this.cart.push({
          productId: productToAdd.id,
          name: productToAdd.name,
          price: productToAdd.price,
          quantity: 1
        });
      }
      this.saveCartToLocalStorage();
    },
    handleUpdateQuantity({ productId, change }) {
      const itemInCart = this.cart.find(item => item.productId === productId);
      if (itemInCart) {
        itemInCart.quantity += change;
        if (itemInCart.quantity <= 0) {
          this.handleRemoveItem(productId);
        }
      }
      this.saveCartToLocalStorage();
    },
    handleRemoveItem(productIdToRemove) {
      this.cart = this.cart.filter(item => item.productId !== productIdToRemove);
      this.saveCartToLocalStorage();
    },
    handleCheckout(totalAmount) {
      if (this.cart.length === 0) {
        alert("Your cart is empty. Add some products before checking out.");
        return;
      }
      alert(`Checkout Successful!\nTotal Amount: $${totalAmount.toFixed(2)}\nThank you for your purchase!`);
      this.cart = [];
      this.saveCartToLocalStorage();
    },
    saveCartToLocalStorage() {
      localStorage.setItem('miniPosCart', JSON.stringify(this.cart));
    },
    loadCartFromLocalStorage() {
      const savedCart = localStorage.getItem('miniPosCart');
      if (savedCart) {
        this.cart = JSON.parse(savedCart);
      }
    }
  },
  created() {
    this.loadCartFromLocalStorage();
  }
};
</script>

